<template>
  <form @submit.prevent="loginProcess">
    <h1>로그인</h1>
    <hr>
    <b>아이디</b>
    <input type="text" v-model="id" name="id" placeholder="Enter id" required>

    <b>비밀번호</b>
    <input type="password" v-model="password" name="password" placeholder="Enter password" required>
    <label>
      <input type="checkbox" name="remember" style="margin-bottom:15px">Remember me
    </label>
    <div class="clearfix">
      <button type="submit" class="submitbtn">로그인</button>
      <button type="button" class="join" @click="join">회원가입</button>
    </div>
  </form>
</template>

<script>
import {useRouter} from 'vue-router'
import {ref} from 'vue';
//npm install vue-cookies
import cookies from 'vue-cookies'; //vue에서 쿠키를 사용하기 위한 cookies를 import합니다.
import axios from '../../axios/axiossetting.js';
export default {
  emits:["parent_getSession"],
  setup(props, context){
    //부모 컴포넌트 App_1.vue에 있는 getSession을 호출합니다.
    //<router-view @parent_getSession="getSession" :parent_id="id" />
    console.log('member_login : setup')
    context.emit("parent_getSession", "");

    const router = useRouter(); //useRouter()는 라우터 인스턴스를 반환합니다.
    const join = () => {
      router.push({
        name:'Join'
      });
    }

    const remember = ref(false);
    const id =ref("");
    const password = ref("");

    const getCookie = () => {
      //이름이 saveid인 쿠키를 가져옵니다.
      const cookie_value = cookies.get("saveid");
      console.log("가져온 쿠키값="+cookie_value);
      if(cookie_value!=null){
        remember.value=true; //체크박스 체크합니다.
        id.value = cookie_value; //쿠키에 저장된 아이디값 input에 나타납니다.
      } else {
        remember.value=false; //체크박스 해제합니다.
      }
    }
    getCookie();

    const loginProcess = async () => {
      try{
              const res = await axios.post("members",{
                id : id.value,
                password: password.value
              });
                  console.log("login.vue:res.data="+res.data);
                  const result = res.data;
                  if(result == -1){
                    alert("아이디가 존재하지 않습니다.");
                  }else if(result == 0){
                    alert("비밀번호가 일치하지 않습니다.");
                  }else if(result == 1){
                    
                    console.log("remember" + remember.value);
                  
                    if(remember.value){ //체크박스 선택한 경우
                      /* 
                          set(name, value, expire) : expire(만료일)을 갖는 name과 value를 가진 쿠키를 설정합니다.
                            1s : 1초
                            1h : 1시간 (60*60)
                            1d : 1일 (24*60*60)
                            1m : 1달
                            new Date(2021,08,30).toUTCString() : 특정 시간
                                      (toUTCString() - 날짜를 UTC(협정 세계시) 형식으로 표현합니다.)
                      */
                    cookies.set("saveid", id.value, "1d"); //1일 동안 유효한 쿠키를 설정합니다.
                    //cookies.set("saveid", id.value, 24*60*60)
                    }else { //체크박스 해제한 경우
                      cookies.remove("saveid"); //쿠키 제거
                    }

                      router.push({
                        name : "Board_List"
                      });
                  }
        }catch(err){
          console.log(err);
        }
      }; // loginProcess end

    return {
      join, id, password, loginProcess, remember
    };
},//setup
};
</script>
<style scoped>
body {font-family: Arial, Helvetica, sans-serif;}

* {box-sizing: border-box}

input{border-radius:3px;border:1px solid lightgray}
input[type=text], input[type=password] {
    width: 100%;
    padding: 10px;
    margin: 5px 0 22px 0;
    display: inline-block;
}


input[type=text]:focus, input[type=password]:focus {
    box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
    outline: none;
}

button {
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    opacity: 0.9;
}
button:nth-child(1){border-radius:3px 0 0 3px}
button:nth-child(2){border-radius:0 3px 3px 0px}
button:hover {
    opacity:1;
    
}
button:focus{
   outline:none;
}


.join{
    padding: 14px 20px;
    background-color: #f44336;
}

.join, .submitbtn {
  height:45px;
  float: left;
  width: 50%;
}
a{color:white;text-decoration:none;}

form {
    background-color: #fefefe;
    margin: 5% auto 15% auto; /* 5% from the top, 15% from the bottom and centered */
    border: 1px solid lightgray;
    width: 500px; /* Could be more or less, depending on screen size */
    padding: 16px;
}

hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
}
 
 h1{text-align:center}

/* Clear floats */
.clearfix::after {
    content: "";
    clear: both;
    display: table; 
}
span{vertical-align: top}

@media screen and (max-width: 300px) {
    .join, .signupbtn {
       width: 100%;
    }
}
</style>